:root {
  --primary-color: #00202e;
  --secondary-color: #003f5c;
  --tertiary-color: #2c4875;

  --c0: #8a508f;
  --c1: #bc5090;
  --c2: #ff6361;
  --c3: #ff8531;
  --c4: #ffa600;
  --c5: #ffd380;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: var(--primary-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: var(--c4);
  text-decoration: none;
  font-style: italic;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Emoji-placeholder {
  font-size: 100pt;
}

.Box-Glow {
  margin: 0;
  padding: 0;
  box-shadow: 0 0 20px 5px var(--tertiary-color);
  background-color: var(--secondary-color);
  border-radius: 10%;
}

.Text-Box {
  padding: 1em;
}
